.checkbox {
    display: flex;
    position: relative;
    cursor: pointer;
    font-size: 13px;
    line-height: 140%;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    align-items: flex-start;

    & input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
        display: none;
    }

    & .checkbox__text {
        line-height: 140%;
    }

    & .checkbox__elem {
        top: 0.1em;
        position: relative;
        height: 1.3em;
        width: 1.3em;
        min-width: 1.3em;
        background-color: #fff;
        display: inline-block;
        margin-right: rem(10);
        border: 1px solid #bcbcbc;

        &::after {
            content: "";
            position: absolute;
            left: 0.15em;
            top: 0.18em;
            width: 9px;
            height: 4px;
            transition: 0.25s cubic-bezier(0.08, 0.82, 0.17, 1);
            opacity: 0;
            background-size: 13px;
            background-position: center;
            background-repeat: no-repeat;
            border-left: 2px solid $main;
            border-bottom: 2px solid $main;
            transform: rotate(0);
            background: transparent;
        }
    }

    & input {
        // &:hover~.checkbox__elem {
        //     border-color: #fff;
        // }

        &:checked~.checkbox__elem {
            background-color: #fff;
            z-index: 2;
        }

        // &:checked~.checkbox__text {
        //     // color: #fff;
        // }

        &:checked~.checkbox__elem:after {
            opacity: 1;
            border-radius: 0;
            transform: rotate(-45deg);
        }
    }

}


